* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
ul{
  list-style: none;
}
img {
  width: 40px;
  height: 40px;
  margin-right: 20px;

}

.country-list {
display: flex;
flex-direction:column;

}

.country_item{
  display: flex;
align-items: center;
font-weight: 600;
  
}
.countries_box{
  display: flex;
align-items: center;
}
